import { Component, ChangeDetectorRef, ViewChild } from '@angular/core';
import { IonicPage, Content, NavController, NavParams, Events, AlertController, LoadingController, Loading, Platform, normalizeURL } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser'
import { ToolsProvider } from '../../providers/tools/tools'
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { TranslateService } from '@ngx-translate/core';
import { NostayServiceProvider } from '../../providers/nostay-service/nostay-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { HomePage } from '../home/home';
import { NativeStorage } from '@ionic-native/native-storage';
import { Hotel } from '../../model/hotel-model';
import { Reservation } from '../../model/reservation-model';
import { GuestData } from '../../model/guest-data-model';
import { GuestServiceProvider } from '../../providers/guest-service/guest-service';
import { Keyboard } from '@ionic-native/keyboard';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

@IonicPage()
@Component({
  selector: 'page-no-stay-in-hotel',
  templateUrl: 'no-stay-in-hotel.html',
  providers: [Keyboard]
})
export class NoStayInHotelPage {
  @ViewChild(Content) content: Content;
  isKeyboardOpen: boolean = false;
  loading: Loading;
  noStayInHotelForm: FormGroup; // login without stay form
  isChecked: boolean = false; // to manage radio button
  htmlData: any = null; // to manage html content
  res2: any; //to store return data while language translate
  radioBtnCls: any = "serve_agree_btn";
  devicePlatForm: any = "android";
  isCheckedPrivacy: boolean = false;
  htmlDataPrivacy: any;

  constructor(private keyboard: Keyboard, public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder, public alertCtrl: AlertController, public sanitized: DomSanitizer, public toolsPvdr: ToolsProvider, public cdRef: ChangeDetectorRef, public iab: InAppBrowser, public translate: TranslateService, public noStayPvdr: NostayServiceProvider, public reservationPvdr: ReservationServiceProvider, public hotelPvdr: HotelServiceProvider, public events: Events, public loadingCtrl: LoadingController, public nativeStorage: NativeStorage, public guestPvdr: GuestServiceProvider, private transfer: FileTransfer, private file: File, private platForm: Platform) {

    this.noStayInHotelForm = formBuilder.group({
      'name': new FormControl('', [Validators.required]),
      'surName': new FormControl('', [Validators.required]),
      'email': new FormControl('', [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)])
    });
    window.addEventListener('keyboardWillHide', (event) => {
      this.isKeyboardOpen = false;
      this.content.scrollToBottom();
    });
    if (this.platForm.is("ios")) {
      this.radioBtnCls = "serve_agree_btn rdo_btn nostay_rdo_btn";
      this.devicePlatForm = "ios";
    }

  }
  fnKeyboardOpen() {
    this.isKeyboardOpen = true;
  }

  ionViewDidLoad() {
    let current = this;
    this.res2 = this.toolsPvdr.fnLanguageTranslate("view.login.label.privacy");
    this.res2.subscribe((value: string) => {
      this.htmlDataPrivacy = this.sanitized.bypassSecurityTrustHtml(value);
      this.cdRef.detectChanges();

      /* click binding to termsofservice span element and redirection */
      let termsElmnt1 = document.querySelector('ion-nav').querySelectorAll("#privacyPolicy");
      for (let i = 0; i < termsElmnt1.length; i++) {
        if (termsElmnt1[i]) {
          termsElmnt1[i].addEventListener('click', function (e) {
            current.iab.create(current.toolsPvdr.getPrivacyUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }
      /* click binding to termsofservice span element and redirection */
      let termsElmnt = document.querySelector('ion-nav').querySelectorAll("#termsOfService");
      for (let i = 0; i < termsElmnt.length; i++) {
        if (termsElmnt[i]) {
          termsElmnt[i].addEventListener('click', function (e) {
            current.iab.create(current.toolsPvdr.getLegalUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }

    });
  }

  ionViewDidEnter() {
    this.keyboard.disableScroll(true);
  }

  /**
   * Function to go back to previous page
   */
  public fnGoBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  /**
   * function to select and deselect the radio button
   * @param  event
   */
  public fnOnRadioSelect($event) {
    this.isChecked = !this.isChecked;
  }

  public fnOnRadioSelectPrivacy($event) {
    this.isCheckedPrivacy = !this.isCheckedPrivacy;
  }


  /**
   * function to valdate and execute after submitting the form
   */
  public fnSubmit() {

    if (!this.noStayInHotelForm.valid && !this.isCheckedPrivacy) {
      let emptyFields = []; // to add empty fields in the form
      let alertMessage: any; // to show alert message on validation
      if (this.noStayInHotelForm.controls['name'].hasError('required')) { // checks name field is empty
        this.toolsPvdr.fnLanguageTranslate("view.login.label.name.title").subscribe(res => {
          emptyFields.push(res);
        });
      }
      if (this.noStayInHotelForm.controls['surName'].hasError('required')) { // checks surName field is empty
        this.toolsPvdr.fnLanguageTranslate("view.login.label.surname.title").subscribe(res => {
          emptyFields.push(res);
        });
      }
      if (this.noStayInHotelForm.controls['email'].hasError('required')) { // checks email field is empty
        this.toolsPvdr.fnLanguageTranslate("view.login.label.mail.title").subscribe(res => {
          emptyFields.push(res);
        });
      }

      // for language translation
      let msg = this.toolsPvdr.fnLanguageTranslateWithParam(["common.mandatoryFields", "view.login.error.emptyMail", "common.error", "buttons.ok"], { fields: emptyFields }); // translate of html content with common funtion

      if (this.noStayInHotelForm.controls['name'].hasError('required')
        || this.noStayInHotelForm.controls['surName'].hasError('required')
        || this.noStayInHotelForm.controls['email'].hasError('required')) {
        msg.subscribe((value: string) => {
          alertMessage = value['common.mandatoryFields'];
        });
        console.log("testerror");
      } else {
        msg.subscribe((value: string) => {
          alertMessage = value['view.login.error.emptyMail'];
        });
      }

      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: alertMessage,
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);

      });
    } else {
      let name = this.noStayInHotelForm.get('name').value;
      let surname = this.noStayInHotelForm.get('surName').value;
      let email = this.noStayInHotelForm.get('email').value;
      let contactphone = '';
      let newsletter = this.isChecked;
      this.showLoading();
      this.noStayPvdr.fnNoStayLoginProceed(name, surname, email, newsletter, contactphone).subscribe(data => {

        if (data.success == true) {
          this.hotelPvdr.objHotel = new Hotel(); //hotel model object
          this.reservationPvdr.objReservation = new Reservation(); //hotel model object
          this.guestPvdr.objGuest = new GuestData(); // nostay guest object
          // set guest data to guest object
          this.guestPvdr.objGuest.name = data.response.name;
          this.guestPvdr.objGuest.reservationId = data.response.reservaId;
          this.guestPvdr.objGuest.lastName = data.response.surname;
          this.guestPvdr.objGuest.email = data.response.email;
          this.guestPvdr.objGuest.phone = data.response.contactphone;
          this.reservationPvdr.objReservation.noStayGuest = true;
          this.reservationPvdr.objReservation.createdDate = data.response.entrada;
          this.reservationPvdr.objReservation.hotelName = data.response.nombreHotel;
          this.reservationPvdr.objReservation.reservationId = data.response.reservaId;
          this.reservationPvdr.objReservation.guestName = data.response.name;
          this.reservationPvdr.objReservation.alreadyRegistered = true;
          this.reservationPvdr.objReservation.registrationCompleted = true;
          // set loacl storage
          this.nativeStorage.setItem('reservationData', { 'localizer': '', 'reservationId': data.response.reservaId, 'name': data.response.name, 'lastname': data.response.surname, 'email': data.response.email, 'registrationCompleted': true, 'alreadyRegistered': true, 'noStayGuest': true, 'response': data.response })
            .then(
              () => console.log('Stored item!'),
              error => console.error('Error storing item', error)
            );

          this.reservationPvdr.getHotelDetails(this.reservationPvdr.objReservation.reservationId).subscribe(data => {

            if (data) {
              this.reservationPvdr.getHotelInfo(data);
              // default receptionist Text translate
              let defaultTextTranslate = this.toolsPvdr.fnLanguageTranslate("view.reception.message");
              let defaultText = '';
              defaultTextTranslate.subscribe((value: string) => {
                defaultText = value;
              });

              //replace guestname and receptionist name in receptionist Text
              let receptionText = this.hotelPvdr.objHotel.receptionistText ? this.hotelPvdr.objHotel.receptionistText : defaultText;
              this.hotelPvdr.objHotel.receptionistText = receptionText.replace('{nombreHuesped}', this.reservationPvdr.objReservation.guestName).replace('{nombreRecepcionista}', this.hotelPvdr.objHotel.receptionistName);
              this.downloadHomePagebackgroundImage();
            }
            // hotel details

          });

        } else if (data) {
          this.loading.dismiss();
          let errmsg = this.toolsPvdr.fnLanguageTranslate(["common.error", "view.login." + data.errorCode, "buttons.ok"], "view.login.error.credentials"); // translate of error message

          errmsg.subscribe((value: string) => {
            let alertnew = this.alertCtrl.create({
              title: value['common.error'],
              message: value["view.login." + data.errorCode] ? value["view.login." + data.errorCode] : value["view.login.error.credentials"],
              cssClass: 'cls_alrt',
              buttons: [
                {
                  text: value['buttons.ok'],
                  role: 'cancel'
                }]
            });
            alertnew.present();
            setTimeout(() => { alertnew.dismiss(); }, 5000);

          });

        }

      },
        error => {
          this.loading.dismiss();
        });
    }
  }
  // download home page background image
  downloadHomePagebackgroundImage() {
    let backgroundImage = this.toolsPvdr.getImagePath(this.hotelPvdr.objHotel.mainImage);
    this.file.checkFile(this.file.cacheDirectory, this.hotelPvdr.objHotel.mainImage + ".jpg").then(() => {

      this.loading.dismiss();
      this.hotelPvdr.objHotel.mainImageUrl = this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg";
      if (this.platForm.is('ios')) {
        this.hotelPvdr.objHotel.mainImageUrl = normalizeURL(this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg");
      }

      this.navCtrl.setRoot(HomePage, {}, { animate: true, animation: 'transition', duration: 1000, direction: 'forward' });

    }, (error) => {
      const fileTransfer: FileTransferObject = this.transfer.create();
      fileTransfer.download(backgroundImage, this.file.cacheDirectory + this.hotelPvdr.objHotel.mainImage + ".jpg").then((entry) => {

        this.loading.dismiss();
        this.hotelPvdr.objHotel.mainImageUrl = entry.toURL();
        if (this.platForm.is('ios')) {
          this.hotelPvdr.objHotel.mainImageUrl = normalizeURL(entry.toURL());
        }
        this.navCtrl.setRoot(HomePage, {}, { animate: true, animation: 'transition', duration: 1000, direction: 'forward' });

      }, (error) => {

      });

    });

  }
  //loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

}



